<template>
  <fusioncharts
    id="10000"
    :type="type"
    width="100%"
    :height="height"
    :dataFormat="dataFormat"
    :dataSource="dataSource">
  </fusioncharts>
</template>

<script>
export default {
  name: 'Column',
  data () {
    const myDataSource = {
      'chart': {
        // 'caption': 'Countries With Most Oil Reserves [2017-18]',
        // 'subCaption': 'In MMbbl = One Million barrels',
        'xAxisName': 'Country',
        // 'yAxisName': 'Reserves (MMbbl)',
        'numberSuffix': 'K',
        'theme': 'fusion'
      },
      'data': [
        {
          'label': 'Venezuela',
          'value': '290'
        },
        {
          'label': 'Saudi',
          'value': '260'
        },
        {
          'label': 'Canada',
          'value': '180'
        },
        {
          'label': 'Iran',
          'value': '140'
        },
        {
          'label': 'Russia',
          'value': '115'
        },
        {
          'label': 'UAE',
          'value': '100'
        },
        {
          'label': 'US',
          'value': '30'
        },
        {
          'label': 'China',
          'value': '30'
        }
      ]
    }

    return {
      type: 'column2d',
      height: '300',
      dataFormat: 'json',
      dataSource: myDataSource
    }
  }
}
</script>

<style scoped>

</style>



