<template>
  <fusioncharts
    :type="type"
    width="100%"
    :height="height"
    :dataFormat="dataFormat"
    :dataSource="dataSource">
  </fusioncharts>
</template>

<script>
export default {
  name: 'Pie',
  data () {
    const myDataSource = {
      'chart': {
        // 'caption': 'Recommended Portfolio Split',
        // 'subCaption': 'For a net-worth of $1M',
        'showValues': '1',
        'showPercentInTooltip': '0',
        'numberPrefix': '$',
        'enableMultiSlicing': '1',
        'theme': 'fusion'
      },
      'data': [{
        'label': 'Equity',
        'value': '300000'
      }, {
        'label': 'Debt',
        'value': '230000'
      }, {
        'label': 'Bullion',
        'value': '180000'
      }, {
        'label': 'Real-estate',
        'value': '270000'
      }, {
        'label': 'Insurance',
        'value': '20000'
      }]
    }
    return {
      type: 'pie2d',
      height: '300',
      dataFormat: 'json',
      dataSource: myDataSource
    }
  }
}
</script>

<style scoped>

</style>



